const util = require('../../../util/util.js');
import refresh from '../../../components/refresh.vue';
import navTab from '../../../components/navTab.vue';
import tabBar4 from '../../../components/tabBar4.vue';
import schoolPicker from '../../../components/schoolPicker/schoolPicker.vue';


const DB = wx.cloud.database().collection("users");
const db = wx.cloud.database().collection("userInfo")
// var _self;

export default {
	components: {refresh,navTab,tabBar4,schoolPicker},

	data(){
		return {
			hasUserInfo: false,
			canIUse: wx.canIUse('button.open-type.getUserInfo'),
			userInfo:{},
			// showGetInfo: false,
			// 弹框
			modalName: "close",
			
			// 表单返回的数据
			isDo:"",
			nickName:"",  // 昵称
			age:"",			
			sex:"男",  
			career:"",
			schoolData: '',
			provinceSchool:"",
			longitude:"", /* 用户当前位置*/
			latitude:"",
			location:"",
		
			sexSwitch: true,   // 这时为男的  数据
			
			index: -1,
			picker: ['学生或高校从业者', '社会精英', '天涯人'],
	
			multiIndex: [0, 0, 0],
			multiArray: [
				['无脊柱动物', '脊柱动物'],
				['扁性动物', '线形动物', '环节动物', '软体动物', '节肢动物'],
				['猪肉绦虫', '吸血虫']
			],

			controls:[{
			  id:1,
			  iconPath:'/static/img/map/map_position.png',
			  position:{
			    left:(global.globalData.windowWidth/2)-36, 
			    top: ((global.globalData.windowHeight-40) / 2)-88 ,
			    width:70, // 20
			    height:113 // 25
			  },
			  clickable: false
			}, {
			    id: 2,
			    iconPath: '/static/img/map/map_recover.png',
			    position: {
			      left: 300, 
			      top: global.globalData.windowHeight-60,
			      width: 40, // 20
			      height: 40 ,// 25
			    },
			    clickable: true
			  }],
			  markers: [{
			    // 红色图标代表 有需求要找人解决的 紧急需要解决 表示 发布任务 状态的人 
			    // 绿色图标 提供服务 的人  查看之后直接找他进行服务便可 
			    // 拓展功能 以自己多少米范围来提供数据
			    iconPath: "",
			    id: 0,
			    latitude: "",
			    longitude: "",
			    width: 20,
			    height: 25
			  }], 

			currentPage:'index',
			tabTitle:['附近','列表'], //导航栏格式 --导航栏组件
			currentTab: 0, //sweiper所在页

		};
	},
	onLoad(e) {
		var _this = this;
		/* 获取用户信息 */
		console.log("测试能否进入循环", global.globalData.userInfo)
		if (global.globalData.userInfo) {
			console.log("测试this.setData函数")
				_this.setData({
				userInfo: global.globalData.userInfo,
				hasUserInfo: true
		    })
			  console.log("已经通过过用户验证", this.userInfo)
		    } else if (this.canIUse){
		      // 由于 getUserInfo 是网络请求，可能会在 Page.onLoad 之后才返回
		      // 所以此处加入 callback 以防止这种情况
		      app.userInfoReadyCallback = res => {
		        _this.setData({
		          userInfo: res.userInfo,
		          hasUserInfo: true
		        })
		      }
			  console.log("通过canIuse进行用户验证", this.userInfo)
		    } else {
		      // 在没有 open-type=getUserInfo 版本的兼容处理
		      wx.getUserInfo({
		        success: res => {
		          global.globalData.userInfo = res.userInfo
		          _this.setData({
		            userInfo: res.userInfo,
		            hasUserInfo: true
		          })
		        }
		      })
			  console.log("通过getUserInfo 获取验证", this.userInfo)
		    }
	},
	
	onHide() {},
	
	onReady(){ // 生命周期函数当 页面加载完毕时执行
		this.mapCtx = wx.createMapContext("mymap"); // 管理地图的剩下文 当点击时
	},
	
	onShow(){
		if (global.globalData.isNewUser){
			this.modalName = "open" 
			console.log("没有注册 打开弹框 获取信息")
		}
		
		console.log("打开首页页面获取定位信息");
		this.getLocation()
		this.getMessage()   
		},
	  
	methods: {
		getGlobalData(){
			
			console.log(global.globalData.userInfo)
			_this.setData({
				userInfo:"wddS",
				hasUserInfo:true
			})
			console.log(this.userInfo)
			console.log(this.hasUserInfo)
			
		},
		
		hidePubSuccessModal(res){
			console.log("执行",res)
			global.globalData.userInfo = res.detail.userInfo
			this.userInfo = res.detail.userInfo,
			this.hasUserInfo = true
			console.log("获取到的用户个人信息",this.userInfo)
			console.log("canIuse", this.canIUse)
		},
		
		
		// 分享页面
		onShareAppMessage(){
		  return { 
		    title:"每个人都是可以让信息产生收益",
		    path:"/pages/index/index"
		  }
		},
		clickList(res){
			console.log("点击list,中的一个卡片跳转个人页面",res, res.myId)
			wx.navigateTo({
			    url: '/pages/detail/detail?id=' + res.currentTarget.id,
			  })
		},
		search(res){
			console.log("输入结束，开始从数据库检索需要的关键词 并存储在marker中",res.target.value)
			this.getSelectInfo(res.target.value)
		},
		
		getSelectInfo(message){
		   DB.where({
		     message:message
		   }).get({
		     success: this.handlegetMessageSucc.bind(this)
		   })
		},
 
		controlTap(){
		  // console.log(e)
		  // 将点移动到原点
		  this.mapCtx.moveToLocation();
		},
		
		handleMarkerTap(e){
		console.log("点击图标跳转详情页面",e,e.markerId)
		wx.navigateTo({
		    url: '/pages/detail/detail?id=' + e.markerId,
		  })
		},
		
		getMessage(){
			DB.get({
			  success: this.handlegetMessageSucc.bind(this),
			})
		  }, 
		handlegetMessageSucc(res){
			console.log("查询数据成功。。。",res.data)
			const data = res.data
			// 请求数据成功之后 进行数据的展示
			const markers = data.map((value, index)=>{
				return {
					iconPath: "/static/img/map/"+ value.type + ".png",
					id: value._id,
					latitude: value.latitude,
					longitude: value.longitude,
					width: 20,
					height: 25,
					message:value.message,
					contact:value.contact,
					type:value.type,
				}
		})
		this.markers = markers
		console.log("获取到全部数据", this.markers)
		},
		  
		getLocation(){
			uni.getLocation({
				type:'gcj02',  
				success: this.handleGetLocaltionSucc.bind(this) // 改变作用域
			})
		},
		
		handleGetLocaltionSucc(res){
			console.log("获得定位数据",res)
			this.longitude = res.longitude,
			this.latitude = res.latitude,
			this.location = [res.longitude, res.latitude]
			
		},
		
		changeTab(index){
			this.currentTab = index,
			console.log(index)
			if (this.currentTab==1){
				uni.navigateTo({
					url:"/pages/taskList/taskList"
				})
			}
		},
		
		// 关闭弹出时触发的事件 
		// 如果该用户的微信id使用过这个软件 则永久不弹出对话框
		// 当用户打开app 是检查信息 是否是历史用户
		hideModal(e) {
			console.log("点击确认之后",e)
			if (e.currentTarget.id == "conformFormList"){ /* 点击最后的确认按钮*/
				/* 对所填数据进行检查确认 */
				if (this.nickName ==""){
					this.showwarning("请填写昵称")
					return;
					}
				if (this.age == ""){
					this.showwarning("请填写年龄")
					return;
					}
				if (this.sex == ""){
					this.showwarning("请选择性别")
					return;
					}
				if (this.career == ""){
					this.showwarning("请填写职业")
					return;
					}
				if ((this.career == "学生或高校从业者" ) && (this.schoolData=="")){
					this.showwarning("请选择学校")
					return;
				}
				this.isDo = true
				
				this.modalName = "close" 
				
				const data = Object.assign({}, {
					age: this.age,nickName:this.nickName,sex:this.sex,career:this.career,schoolData:this.schoolData,
					provinceSchool:this.provinceSchool, isDo:this.isDo,location:this.location
				  })
				db.add({
				  data:data
				  ,success(res){
					  console.log("新用户注册 提交数据成功",res)
					  global.globalData.isNewUser = false
				  }
				})
				console.log(this.age, this.nickName,this.sex,this.schoolData,this.career)
			}
		},
		showwarning(title){
		wx.showToast({
		  title: title,
		  icon:"loading",
		  duration: 2000
		})
		},
		
		sexSwitchChange(e) {
			this.sexSwitch = e.detail.value
			console.log("是男是女",e)
			if (this.sexSwitch){
				this.sex = "男"
			}
			else{
				this.sex="女"
			} 
		},
		MultiChange(e) {
			this.multiIndex = e.detail.value
		},
		// 选择职业
		PickerChange(e) {
			this.index = e.detail.value
			this.career = this.picker[this.index]
		},
			// 多列选择获得具体信息
		MultiColumnChange(e) {
			let data = {
				multiArray: this.multiArray,
				multiIndex: this.multiIndex
			};
			data.multiIndex[e.detail.column] = e.detail.value;
			switch (e.detail.column) {
				case 0:
					switch (data.multiIndex[0]) {
						case 0:
							data.multiArray[1] = ['扁性动物', '线形动物', '环节动物', '软体动物', '节肢动物'];
							data.multiArray[2] = ['猪肉绦虫', '吸血虫'];
							break;
						case 1:
							data.multiArray[1] = ['鱼', '两栖动物', '爬行动物'];
							data.multiArray[2] = ['鲫鱼', '带鱼'];
							break;
					}
					data.multiIndex[1] = 0;
					data.multiIndex[2] = 0;
					break;
				case 1:
					switch (data.multiIndex[0]) {
						case 0:
							switch (data.multiIndex[1]) {
								case 0:
									data.multiArray[2] = ['猪肉绦虫', '吸血虫'];
									break;
								case 1:
									data.multiArray[2] = ['蛔虫'];
									break;
								case 2:
									data.multiArray[2] = ['蚂蚁', '蚂蟥'];
									break;
								case 3:
									data.multiArray[2] = ['河蚌', '蜗牛', '蛞蝓'];
									break;
								case 4:
									data.multiArray[2] = ['昆虫', '甲壳动物', '蛛形动物', '多足动物'];
									break;
							}
							break;
						case 1:
							switch (data.multiIndex[1]) {
								case 0:
									data.multiArray[2] = ['鲫鱼', '带鱼'];
									break;
								case 1:
									data.multiArray[2] = ['青蛙', '娃娃鱼'];
									break;
								case 2:
									data.multiArray[2] = ['蜥蜴', '龟', '壁虎'];
									break;
							}
							break;
					}
					data.multiIndex[2] = 0;
					break;
			}
			this.multiArray = data.multiArray;
			this.multiIndex = data.multiIndex;
		},
		
		// 这里是学生的城市 学校选择区
		showSchoolPicker: function() {
			this.$refs.schoolPicker.show()
		},
		onConfirm(e) {
			console.log("选择的结果label",e.label)
			const school = e.label.split("-")[2];
			console.log(`源数据:${JSON.stringify(e)}`);
			if (school === '暂未收录') {
				return;
			} else {
				this.provinceSchool = e.label
				this.schoolData = school;
				console.log(`学校:${school}`);
			}
		},
		nikeNameInput(e){
			console.log("输入",e,e.detail,e.detail.value)
			this.nickName = e.detail.value
		},
		ageInput(e){
			console.log("输入年龄",e,e.detail,e.detail.value)
			this.age = e.detail.value
		}
	}
};
